<template>
  <div class="select is-large is-fullwidth">
    <select aria-label="Select language" v-model="choosed" autofocus>
      <option disabled hidden>Select language</option>
      <option
        v-for="{ slug, name } in languages"
        :value="slug"
        :key="slug"
      >
        {{ name }}
      </option>
    </select>
    <div class="browse">
      <router-link :to="{ name: 'languages' }" class="button">‚ö°Ô∏è Browse all languages</router-link>
    </div>
  </div>
</template>

<script>
export default {
  name: 'language-picker',
  props: {
    language: String,
    languages: Array,
    placeholder: String,
  },
  data: () => ({
    choosed: '',
  }),
  created() {
    this.choosed = this.$route.params.name || this.placeholder;
  },
  watch: {
    choosed() {
      if (this.choosed !== this.placeholder) {
        this.$emit('input', this.choosed);
      }
    },
  },
};
</script>

<style scoped>
select {
  padding: 0 1.2rem;
  -webkit-box-shadow: 0 2px 3px rgba(10, 10, 10, 0.1),0 0 0 1px rgba(10, 10, 10, 0.1) !important;
  box-shadow: 0 2px 3px rgba(10, 10, 10, 0.1), 0 0 0 1px rgba(10, 10, 10, 0.1) !important;
}
.select {
  margin-bottom: 6rem;
}

select:focus, .select:not(.is-multiple)::after {
  border-color: #FFF488;
}

.browse {
  text-align: center;
  margin-top: 1rem;
  font-size: 1.5rem;
}

</style>



