<template>
  <main class="container">
    <language-picker
      v-model="language"
      :languages="languages"
      :placeholder="'Select language'"
    ></language-picker>
    <router-view></router-view>
  </main>
</template>

<script>
import { getLanguages } from '@/api';
import LanguagePicker from '@/components/LanguagePicker';

export default {
  name: 'app-main',
  components: {
    LanguagePicker,
  },
  data: () => ({
    language: '',
    languages: [],
  }),
  created() {
    this.languages = getLanguages();
  },
  watch: {
    language() {
      const url = this.language ? `/languages/${this.language}` : '';
      this.$router.push(url);
    },
  },
};
</script>

<style scoped>
.container {
  width: 90%;
}
</style>



