import Vue from 'vue';
import VueScrollTo from 'vue-scrollto';
import 'bulma/css/bulma.css';
import 'animate.css';

import router from './router';
import App from './App';

Vue.use(VueScrollTo);

Vue.config.productionTip = false;

/* eslint-disable no-new */
new Vue({
  el: '#app',
  template: '<App/>',
  components: { App },
  router,
});



