const Index = () => import('@/pages/Index');
const Language = () => import('@/pages/Language');
const Languages = () => import('@/pages/Languages');
const Quiz = () => import('@/pages/Quiz');

const routes = [
  {
    path: '*',
    name: 'index',
    component: Index,
  },
  {
    path: '/languages',
    name: 'languages',
    component: Languages,
  },
  {
    path: '/languages/:name',
    name: 'language',
    component: Language,
  },
  {
    path: '/languages/:name/quiz',
    name: 'quiz',
    component: Quiz,
  },
];

export default routes;



