import languages from '@/data/languages';
import words from '@/data/words';

/* eslint-disable arrow-body-style */
/* eslint-disable no-param-reassign */
/* eslint-disable no-return-assign */

function getLanguages() {
  return languages;
}

function getCategories() {
  const categories = [];
  words.forEach((word) => {
    const category = categories.find(({ name }) => name === word.category);
    if (!category) {
      categories.push({
        name: word.category,
        words: [word],
      });
    } else {
      category.words.push(word);
    }
  });
  return categories;
}

function getWordCount() {
  return words.length;
}

function getTranslatedWordCount(language) {
  return words.reduce((total, { translations }) => {
    return translations[language] ? total += 1 : total;
  }, 0);
}

export {
  getLanguages,
  getCategories,
  getWordCount,
  getTranslatedWordCount,
};



